}

.gdesc-closed .gslide-media {
  transition: opacity 0.5s ease;
  opacity: 1;
}

.greset {
  transition: all 0.3s ease;
}

.gabsolute {
  position: absolute;
}

.grelative {
  position: relative;
}

.glightbox-desc {
  display: none !important;
}

.glightbox-open {
  overflow: hidden;
}

.gloader {
  animation: lightboxLoader 0.8s infinite linear;
  position: absolute;
  z-index: 9999;
  left: 0;
  right: 0;
  top: 47%;
  display: block;
  height: 1.5625rem;
  width: 1.5625rem;
  margin: 0 auto;
  border: 2px solid var(--g-clr-white);
  border-right-color: transparent;
